.imageLightBoxView {
    text-align: center;
    box-sizing: border-box;
    display: flex;
    height: calc(100vh - 54px);
    -webkit-box-align: center;
    align-items: center;
    -webkit-box-pack: center;
    justify-content: center;
    padding-left: 80px;
    padding-right: 80px;
    position: relative;


    .shoppingButton {
        position: absolute;
        bottom: 10px;
        right: 10px;
        width: 70px;
        height: 70px;
        border-radius: 50px;
        background-color: #fff;
        border: 1px solid #dbdcde;
        display: flex;
        justify-content: center;
        align-items: center;
        font-size: 2em;

        &.showed {
            background-color: #e4e4e4;
        }

    }

    .imgContainer {
        height: calc(100vh - 54px);
        flex: 1 1 auto;
display: flex;
    justify-content: center;
    align-items: center;
        &.margin-80 {
            margin-right: 40px;
        }
    }

    img {
        max-height: 100%;
        height: auto;
        max-width: 100%;
        user-select: none;
    }

    .shoppingContainer {
     
        margin-left: 20px;
        position: relative;
        width: 20%;

         @media screen and (min-width: 1550px) {
        margin-left: 0;
        }

        &.mobile {
            right: 0;
            position: absolute;
            margin: 0;
        }
    }
}

.mobileView {
    .imageLightBoxView {
        padding-left: 0;
        padding-right: 0;

        img {
            max-width: 100vw;
        }

        .imgContainer {
            height: 100%;
            &.margin-80 {
                margin-right: 0;
            }
    }
        
    }
}