.imageLightBoxContainer {
    display: flex;
    height: 100%;
    box-sizing: border-box;
    -webkit-box-orient: vertical;
    -webkit-box-direction: normal;
    flex-direction: column;
    background-color: #f4f5f7;
    overflow: hidden;

    main {
        margin: 0px 
    }

     .react-images__navigation {
        & > button {
            box-shadow: rgba(0, 0, 0, 0.18) 0px 1px 6px;
            background-color:#fff;
            opacity: 0;

            svg {
                color: #6B7774;
            }

            &:hover {

                svg {
                    color: #000;
                } 
            }
        }
    }

    &:hover {
        .react-images__navigation {
             & > button {
                opacity: 1;
             }
        }
    }

    .react-images__pager {
        height: calc(100% - 54px);
    }

    .react-images__frame, .react-images__track {
        height: 100%;
    }
}

.react-images__positioner {
    & > div:nth-child(3) {
        height: 100%;
    }

    .react-images__dialog {
        height: 100%;
    }

    .react-images__view-wrapper {
        height: 100%;

         & > div {
             height: 100%;
         }
    }
}