

.lightBoxShopping {
    display: flex;
    flex-direction: column;
    //border: 1px solid #d6d6d6;
    justify-content: space-around;
    align-items: center;
    font-size: 1.2em;
    color: #6B7774;
    padding: 15px;
    border-radius: 15px;
    padding-bottom: 30px;
    width: 200px;
    margin: auto;

    @media screen and (min-width: 1550px) {
        position:relative;
        right: 15%;
    }

    

    .header {
        margin-bottom: 20px;
        font-size: 14px;
        cursor: pointer;
        z-index: 1;
        img {
            height: 60px;

        }
    }

    &.showed {
        box-shadow: rgba(0, 0, 0, 0.18) 0px 1px 6px;
    }

    .formatData {
        width: 100%;
        z-index: 1;
        &:not(:last-child) {
            @media screen and (min-width: 1100px) {
                 padding-bottom: 10px;
            }
          
            margin-bottom: 10px;
            ///border-bottom: 1px solid #d6d6d6;
        }

        .formatName {
            font-weight: bold;
        }
    }

    .background {
        position: absolute;
        top:0;
        left: 0;
        width: 100%;
        height: 100%;
        background-color: #f4f5f7;
        opacity: 0.5;
        border-radius: 15px;
    }

    .formatButtons {
        display: flex;
        justify-content: center;
        align-items: center;

        .formatCount {
            width: 50px;
            height: 50px;
            text-align: center;

            &::-webkit-outer-spin-button,
            &::-webkit-inner-spin-button {
            -webkit-appearance: none;
            margin: 0;
            }

            /* Firefox */
            &[type=number] {
            -moz-appearance: textfield;
            }
        }

        .formatButton {
            width: 50px; 
            height: 50px;
            cursor: pointer;
            border-radius: 50%;
            display: flex;
            justify-content: center;
            align-items: center;
            margin: 0 5px;
            &:hover {
                color: #000;
            }
        }
    }
}

.imageLightBoxContainer.mobileView {
    .shoppingContainer {
        left: 0;
        top:0;
        width: 100%;
        height: 100%;
        
    }

    .lightBoxShopping {
        height: 100%;
        border-radius: 0;
        box-shadow: none;
        overflow:hidden;
       
        &.showed {
            backdrop-filter: blur(15px);
             background: #ffffff9c ;
            overflow: scroll; 
        }

        .background {
            border-radius: 0;
            background-color: #ffffff;
            opacity: 0.2;
        }
    }
}

.mobile {
    .lightBoxShopping  {
        width: 100%;
    }
}