.imageLightboxHeader {
    background-color: white;
    box-shadow: rgba(0, 0, 0, 0.1) 0px 1px 0px;
    color: rgb(37, 56, 88);
    display: flex;
    height: 54px;
    -webkit-box-align: center;
    align-items: center;
    -webkit-box-pack: justify;
    justify-content: space-between;
    flex: 0 0 auto;
    width: 100%;
    padding: 0 20px;

    .leftContainer {
        display:flex;
        flex-direction:column;
        max-width: 80%;
        overflow: hidden;
        .folderName {
            font-weight: bold;
        }
        .fileName {
            white-space: nowrap;
            text-overflow: ellipsis;
        }
    }
    .rightContainer {
        display: flex;
        font-size: 1.5em;
        
        .closeButton, .shoppingButton, .favouriteButton  {
            color: #6B7774;

            &:hover {
                color: #000000;
            }
        }

        .closeButton {
            margin-left: 20px;
            padding-left: 20px;
            border-left: 1px solid #ebecf0;
            cursor: pointer;
        }

        .shoppingButton {
            cursor: pointer;
             margin-left: 20px;
            padding-left: 20px;
            border-left: 1px solid #ebecf0;

            img {
                height: 28px; 
                position: relative;
                top: -1px;
            }
        }

        .favouriteButton {
            cursor: pointer;
        }
    }
}