.client-container-header-checkout {
        z-index: 2;
    height: 54px;
    background: rgba(255,255,255,.81);
    width: 100%;
    position: fixed;
    top: 0;
    left: 0;

    .right-favourite-button {
        display: none;
    }

    .btn-light {
        border-color: #bfbfbf;
    }

     @media screen and (min-width: 663px ) {
           .center-favourite-button {
                display:none;
            }

             .right-favourite-button {
                display:block;
            }
    }
        
}