.photosView {
    .removeButtonContainer {
        display: none;
        position: absolute;
        top: 10px;
        right: 10px;
        background-color: #fff;
        width: 30px;
        height: 30px;
        justify-content: center;
        align-items: center;
        border-radius: 50%;

        .icon {
            font-size: 1.5em;
        }
    }

    .gallery-image-link {
        padding: 0;
        margin: 0;
        cursor: pointer;
        position: relative;

        &:hover {
            .removeButtonContainer {
                display: flex;
            }
        }
    }
}