.eventFolderPhotos {
    .photoImage {
        position: relative;
        cursor: pointer;

        .adminMenu {
            position: absolute;
            top:10px;
            right:10px;
            font-size: 2em;
            display: none;
            border-radius: 50%;
            justify-content: center;
            align-items: center;
            width: 2em;
            height: 2em;
            overflow: hidden;
            color: #000;

            .adminMenuBackground {
                position: absolute;
                top:0;
                left: 0;
                width: 100%;
                height: 100%;
                background-color: #fff;
                opacity: 0.3;
            }
        }

        .menu {
            position: absolute;
            bottom:0;
            left: 0;
            width: 100%;
            overflow: hidden;
            display: flex;
            justify-content: space-between;
            align-items: center;
            height: 0;
            transition: height 0.15s ease-out;

            .menuItem {
                display: flex;
                justify-content: flex-end;
                align-items: flex-end;
                width: 50%;
                height: 50px;
                color: #fff;
                font-size: 2em;
                cursor: pointer;
                z-index: 1;

                &.leftMenu {
                    justify-content: flex-start;
                  

                    .fa-heart {
                        font-size: 24px;
                        margin: 0 0 5px 4px;

                        @media screen and (max-width: 1100px) {
                            font-size: 16px;
                            margin: 0 0 6px 4px;
                        }
                    }
                }

                .fas {
                    margin-right: 10px;
                    margin-bottom: 10px;
                    font-size:20px;
                }

                .heartIcon {
                    height: 30px;
                    margin-bottom: 3px;
                    margin-left: 1px;
                }

                .cartIcon {
                    height: 30px;
                    margin-right: 3px;
                    margin-bottom: 3px;

                    @media screen and (max-width: 1100px) {
                        height:20px;
                    }
                }
            }

            .menuBackground {
                position: absolute;
                top: 0;
                left: 0;
                background: linear-gradient(0deg, rgba(255,255,255,1) 0%, rgba(255,255,255,0) 50%);
                opacity: 1;
                width: 100%;
                height: 100%;

                @media screen and (max-width: 1100px) {
                        height:60%;
                        bottom: 0;
                        top: unset;
                    }
            }

            .menuFormat {
                 display: flex;
                justify-content: flex-end;
                align-items: flex-end;
                width: 50%;
                height: 50px;
                color: #fff;
                font-size: 2em;
                cursor: pointer;
                z-index: 1;
                margin-right: 5px;
                margin-bottom: 15px;

                .format {
                    display: flex;
                    justify-content: center;
                    align-items: center;
                    width: 25px;
                    height: 25px;
                    border-radius: 50%;
                    color: #7b7b7b;
                    font-size: 15px;
                    background-color: #fff;
                    box-shadow: rgba(0, 0, 0, 0.18) 0px 1px 6px;
                }
            }
        }

        &:hover, &.hovered {
            .menu {
                height: 50px;
                transition: height 0.15s ease-in;

            }

            .adminMenu {
                display: flex;
                cursor: pointer;
                &:hover {
                    background-color: #fff;
                }
            }
        }
    }

    .loader {
        width: 100%;
        height: 10vh;
        display:flex;
        justify-content: center;
        align-items: center;
        font-size: 2em;

        .fas {
            margin-right: 10px;
        }
    }
}

.paginationContainer {
        text-align: center;
        width: 100%;
        font-size: 20px;

        @media screen and (max-width: 480px) {
            font-size: 16px;
        }

        @media screen and (max-width: 340px) {
            font-size: 12px;
        }

        .background {
            display: none;
        }

        &.fixed {
            position: fixed;
            bottom: 0;
            left: 0;
            display: flex;
            justify-content: center;
            align-items: center;
            z-index: 1;
            padding: 10px;
            border-top: 1px solid #dcdcdc;

            .background {
                background-color: #fff;
                width: 100%;
                height: 100%;
                position: absolute;
                display: block;
                opacity: 0.9;
            }

            .pagination {
                margin:0
            }

            .pagination {
                & > li {
                    & > a {
                        position: relative;
                        float: left;
                        padding: 6px 12px;
                        margin-left: -1px;
                        line-height: 1.42857143;
                        text-decoration: none;
                        background-color: #fff;
                        border: 1px solid #ddd;
                        border-color: #b3b3b3 ;
                    }
                }
            }
        }

        .pagination {
            .previous, .next {
                a {
                    padding: 6px 40px;
                    
                }
                
            }

            & > li {
                cursor: pointer;
                & > a {
                    color: #555;
                    outline: none;
                }
            }

            & > .active {
                a {
                    color: #000 !important;
                    background-color: #cacaca;
                    border-color: #1f1f1f;
                }
            }
        }
    
    &.eventFolderPhotosMobile {
        .heartIcon {
            height:23px !important;
        }

        .menuFormat 
        {
            margin-right: 3px !important;
            margin-bottom: 8px !important;
            
            .format {
                width: 20px !important;
                height: 20px !important;
            }
        }

       

    }
}

 